:root {
  --font-body: "Karla", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --cyan: hsl(179, 62%, 43%);
  --cyanlighter: hsl(179, 62%, 53%);
  --brightyellow: hsl(71, 73%, 54%);
  --liightgray: hsl(204, 43%, 93%);
  --grayishblue: hsl(218, 22%, 67%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  background: #833ab4;
  background: linear-gradient(90deg, #833ab4 0%, #fd1d1d 50%, #fcb045 100%);
}

h1,
h2,
h3 {
  margin: 0;
  padding: 0;
  line-height: 1.3;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  margin: 10rem auto;
  padding: 0;
  max-width: 90rem;
  text-align: center;
}

.price {
  display: grid;
  justify-content: center;
  text-align: start;
  grid-template-columns: minmax(311px, 357px) minmax(311px, 357px);
  grid-template-rows: 15.3125rem 18.1875rem;
  grid-template-areas: "community community" "subscription whyus";
}
@media (max-width: 48em) {
  .price {
    grid-template-columns: minmax(311px, 715px);
    grid-template-rows: 15.3125rem 18.1875rem 18.1875rem;
    grid-template-areas: "community" "subscription" "whyus";
  }
}
@media (max-width: 25.875em) {
  .price {
    grid-template-rows: 16.6875rem 14.25rem 14.25rem;
  }
}
.price-community {
  background-color: var(--white);
  padding: 0 3rem;
  border-radius: 10px 10px 0 0;
  grid-area: community;
}
@media (max-width: 25.875em) {
  .price-community {
    padding: 0 1.5rem;
    border-radius: 5px 5px 0 0;
  }
}
.price-community h1 {
  margin-top: 2rem;
  margin-bottom: 1rem;
  color: var(--cyan);
  font-weight: 700;
  font-size: 1.8rem;
}
@media (max-width: 25.875em) {
  .price-community h1 {
    font-size: 1.3rem;
    margin-top: 1.6rem;
    margin-bottom: 0;
  }
}
.price-community h2 {
  padding: 0;
  margin: 0;
  margin-top: 1.5rem;
  color: var(--brightyellow);
  font-size: 1.3rem;
}
@media (max-width: 25.875em) {
  .price-community h2 {
    font-size: 1rem;
    margin-top: 1.2rem;
  }
}
.price-community p {
  padding: 0;
  margin: 0;
  margin-top: 0.6rem;
  color: var(--grayishblue);
  font-size: 1.1rem;
  line-height: 1.7;
  font-weight: 700;
}
@media (max-width: 25.875em) {
  .price-community p {
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.8;
    margin-top: 1rem;
  }
}
.price-subscription {
  background-color: var(--cyan);
  color: var(--white);
  padding: 0 3rem;
  border-radius: 0 0 0 10px;
  grid-area: subscription;
}
@media (max-width: 48em) {
  .price-subscription {
    border-radius: 0;
  }
}
@media (max-width: 25.875em) {
  .price-subscription {
    padding: 0 1.5rem;
  }
}
.price-subscription h2 {
  font-size: 1.3rem;
  margin-top: 2.5rem;
  margin-bottom: 1rem;
}
@media (max-width: 25.875em) {
  .price-subscription h2 {
    font-size: 1.1rem;
    margin-top: 1.6rem;
    margin-bottom: 0;
  }
}
.price-subscription h3 {
  font-size: 2.5rem;
}
@media (max-width: 25.875em) {
  .price-subscription h3 {
    font-size: 2rem;
    margin-top: 1rem;
  }
}
.price-subscription h3 span {
  position: relative;
  top: -8px;
  left: 0;
  font-size: 1rem;
  padding: 1rem;
  color: var(--brightyellow);
}
.price-subscription p {
  margin: 0;
  padding: 0;
  margin-top: 0.5rem;
  font-size: 1rem;
}
.price-subscription button {
  margin: 2rem auto;
  padding: 1rem 3rem;
  background-color: var(--brightyellow);
  border: none;
  border-radius: 5px;
  display: block;
  width: 100%;
  font-size: 1.2rem;
  color: var(--white);
  box-shadow: 10px 10px --var(--brightyellow);
  cursor: pointer;
}
.price-subscription button:hover {
  opacity: 0.9;
}
@media (max-width: 25.875em) {
  .price-subscription button {
    font-size: 0.9rem;
    font-weight: 600;
    margin-top: 1.7rem;
    letter-spacing: 1px;
  }
}
.price-whyus {
  background-color: var(--cyanlighter);
  padding: 0 3rem;
  border-radius: 0 0 10px 0;
  grid-area: whyus;
  color: var(--white);
}
@media (max-width: 48em) {
  .price-whyus {
    border-radius: 0 0 10px 10px;
  }
}
@media (max-width: 25.875em) {
  .price-whyus {
    padding: 0 1.5rem;
    border-radius: 0 0 5px 5px;
  }
}
.price-whyus h2 {
  font-size: 1.3rem;
  margin-top: 2.5rem;
  margin-bottom: 1rem;
}
@media (max-width: 25.875em) {
  .price-whyus h2 {
    font-size: 1.1rem;
    margin-top: 1.6rem;
    margin-bottom: 1.2rem;
  }
}
.price-whyus ul {
  list-style: none;
  text-align: start;
  margin: 0;
  padding: 0;
  line-height: 1.4;
}
@media (max-width: 25.875em) {
  .price-whyus ul {
    font-size: 0.9rem;
    line-height: 1.4;
  }
}